
.seller {
  position: absolute;
  top: 174px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}
.seller .overview {
    padding: 18px;
}
.seller .overview .title {
      margin-bottom: 8px;
      line-height: 14px;
      color: #07111b;
      font-size: 14px;
}
.seller .overview .desc {
      padding-bottom: 18px;
      font-size: 0;
      position: relative;
}
.seller .overview .desc::after {
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        border-top: 1px solid rgba(7, 17, 27, 0.1);
        content: '';
}
.seller .overview .desc .star {
        display: inline-block;
        margin-right: 8px;
        vertical-align: top;
}
.seller .overview .desc .text {
        margin-right: 12px;
        display: inline-block;
        line-height: 18px;
        vertical-align: top;
        font-size: 10px;
        color: #4d505d;
}
.seller .overview .remark {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      padding-top: 18px;
}
.seller .overview .remark .block {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        text-align: center;
        border-right: 1px solid rgba(7, 17, 27, 0.1);
}
.seller .overview .remark .block:last-child {
          border: none;
}
.seller .overview .remark .block h2 {
          margin-bottom: 4px;
          line-height: 10px;
          font-size: 10px;
          color: #93999f;
}
.seller .overview .remark .block .content {
          line-height: 24px;
          font-size: 10px;
          color: #93999f;
}
.seller .overview .remark .block .content .stress {
            font-size: 24px;
            color: #07111b;
}



