
.ratings {
  position: absolute;
  top: 174px;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
}
.ratings .overview {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 10px 0;
}
.ratings .overview .overview-left {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 137px;
              flex: 0 0 137px;
      padding: 6px 0;
      width: 137px;
      text-align: center;
      border-right: 1px solid rgba(7, 17, 27, 0.1);
}
@media only screen and (max-width: 320px) {
.ratings .overview .overview-left {
          -webkit-box-flex: 0;
              -ms-flex: 0 0 120px;
                  flex: 0 0 120px;
          width: 120px;
}
}
.ratings .overview .overview-left .score {
        margin-bottom: 6px;
        line-height: 18px;
        font-size: 24px;
        color: #ff9900;
}
.ratings .overview .overview-left .title {
        margin-bottom: 8px;
        line-height: 12px;
        font-size: 12px;
        color: #07111b;
}
.ratings .overview .overview-left .rank {
        line-height: 10px;
        font-size: 10px;
        color: #93999f;
}
.ratings .overview .overview-right {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      padding: 6px 0 6px 24px;
}
@media only screen and (max-width: 320px) {
.ratings .overview .overview-right {
          padding-left: 6px;
}
}
.ratings .overview .overview-right .score-wrapper {
        margin-bottom: 8px;
        line-height: 18px;
        font-size: 0;
}
.ratings .overview .overview-right .score-wrapper .title {
          display: inline-block;
          vertical-align: top;
          line-height: 18px;
          font-size: 12px;
          color: #07111b;
}
.ratings .overview .overview-right .score-wrapper .star {
          display: inline-block;
          margin: 0 12px;
          vertical-align: top;
}
.ratings .overview .overview-right .score-wrapper .score {
          display: inline-block;
          line-height: 18px;
          vertical-align: top;
          font-size: 12px;
          color: #ff9900;
}
.ratings .overview .overview-right .delivery-wrapper {
        font-size: 0;
}
.ratings .overview .overview-right .delivery-wrapper .title {
          line-height: 12px;
          font-size: 12px;
          color: #07111b;
}
.ratings .overview .overview-right .delivery-wrapper .delivery {
          margin-left: 15px;
          font-size: 12px;
          color: #93989f;
}



