
.cartcontrol {
  font-size: 0;
}
.cartcontrol .cart-decrease {
    display: inline-block;
    padding: 6px;
    line-height: 24px;
    font-size: 24px;
    color: #00a0ff;
    -webkit-transform: translate3D(0, 0, 0) rotate(0);
            transform: translate3D(0, 0, 0) rotate(0);
}
.cartcontrol .cart-decrease.move-enter-active, .cartcontrol .cart-decrease.move-leave-active {
      transition: all .4s linear;
}
.cartcontrol .cart-decrease.move-enter, .cartcontrol .cart-decrease.move-leave-active {
      opacity: 0;
      -webkit-transform: translate3D(40px, 0, 0) rotate(360deg);
              transform: translate3D(40px, 0, 0) rotate(360deg);
}
.cartcontrol .cart-count {
    display: inline-block;
    vertical-align: top;
    width: 12px;
    padding-top: 6px;
    line-height: 24px;
    text-align: center;
    font-size: 10px;
    color: #93999f;
}
.cartcontrol .cart-add {
    display: inline-block;
    padding: 6px;
    line-height: 24px;
    font-size: 24px;
    color: #00a0ff;
}



